  import urls from '../../utils/urls.js'
  export default {
    data () {
      return {
        bottomList: [],
        // lists: [],
				selectList:[],
      }
    },
    onLoad(content) {
      uni.request({
        url: urls.HTTP + '/index/searchnovel',
        method: 'POST',
        data: {
          keyword: content.content
        },
        success: (res) => {
          console.log(res)
          this.selectList = res.data
        }
      })
    },
    methods: {
      goDetail: function (id) {
        uni.navigateTo({
          url: '../common/book/storyDetail?id=' + id
        })
      }
    }
  }
